.relevant-project-item {
  display: flex;
  margin-bottom: 50px;
  align-items: flex-start;
  flex-flow: row wrap; }
  .relevant-project-item .name {
    position: relative;
    display: flex;
    width: 120px;
    height: 72px;
    align-items: center;
    flex-flow: row wrap;
    justify-content: center;
    text-transform: inherit;
    color: #32333c;
    font-family: inherit;
    font-size: 14px;
    font-weight: 500; }
    .relevant-project-item .name::after {
      position: absolute;
      top: 0;
      left: 0;
      width: 120px;
      height: 72px;
      content: '';
      background: url("../../../../../assets/images/macbook.png") no-repeat center;
      background-size: cover; }
  .relevant-project-item .summary {
    flex: 1;
    padding-left: 20px; }
    .relevant-project-item .summary .description {
      padding-bottom: 5px; }
    .relevant-project-item .summary .role {
      text-transform: inherit;
      color: #32333c;
      font-family: inherit;
      font-size: 14px;
      font-weight: 500;
      padding-bottom: 10px; }
    .relevant-project-item .summary .duration {
      text-transform: inherit;
      color: inherit;
      font-family: "Poppins", sans-serif;
      font-size: 14px;
      font-weight: 100; }
