.background {
  background-color: #ffffff; }
  .background .container {
    display: flex;
    padding: 100px 0;
    background-color: #ffffff;
    align-items: center;
    flex-flow: column wrap; }
    .background .container .title {
      padding-bottom: 25px; }
      @media (min-width: 1000px) {
        .background .container .title {
          text-transform: inherit;
          color: #32333c;
          font-family: inherit;
          font-size: 45px;
          font-weight: 700; } }
      @media (max-width: 1000px) {
        .background .container .title {
          text-transform: inherit;
          color: #32333c;
          font-family: inherit;
          font-size: 35px;
          font-weight: 600; } }
    @media (min-width: 1000px) {
      .background .container .content {
        width: 1000px; } }
    @media (max-width: 1000px) {
      .background .container .content {
        width: calc(100% - 30px); } }
