.background {
  background-color: #ffffff; }
  .background .container {
    display: flex;
    padding: 100px 0;
    background-color: #ffffff;
    align-items: center;
    flex-flow: column wrap; }
    .background .container .title {
      padding-bottom: 25px; }
      @media (min-width: 1000px) {
        .background .container .title {
          text-transform: inherit;
          color: #32333c;
          font-family: inherit;
          font-size: 45px;
          font-weight: 700; } }
      @media (max-width: 1000px) {
        .background .container .title {
          text-transform: inherit;
          color: #32333c;
          font-family: inherit;
          font-size: 35px;
          font-weight: 600; } }
    @media (min-width: 1000px) {
      .background .container .content {
        width: 1000px; } }
    @media (max-width: 1000px) {
      .background .container .content {
        width: calc(100% - 30px); } }

.content {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between; }
  .content .introduction {
    display: flex;
    padding: 25px 0;
    align-items: flex-start;
    flex-flow: column wrap; }
    .content .introduction .greeting {
      text-transform: inherit;
      color: #32333c;
      font-family: inherit;
      font-size: 18px;
      font-weight: 500;
      padding-bottom: 15px; }
    @media (min-width: 1000px) {
      .content .introduction {
        flex: 0.45; } }
    @media (max-width: 1000px) {
      .content .introduction {
        flex: auto; } }
  .content .skills {
    display: flex;
    padding: 25px 0;
    align-items: flex-end;
    flex-flow: column wrap; }
    @media (min-width: 1000px) {
      .content .skills {
        flex: 0.45; } }
    @media (max-width: 1000px) {
      .content .skills {
        flex: auto; } }
