.progress-bar-item {
  display: flex;
  flex-flow: column wrap; }
  .progress-bar-item:not(:first-child) {
    margin-top: 25px; }
  .progress-bar-item .header {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between; }
    .progress-bar-item .header .name,
    .progress-bar-item .header .value {
      text-transform: inherit;
      color: #32333c;
      font-family: "Poppins", sans-serif;
      font-size: 16px;
      font-weight: 400; }
  .progress-bar-item .bar {
    overflow: hidden;
    height: 5px;
    margin-top: 15px;
    border-radius: 5px;
    background-color: #d9d9d9; }
    .progress-bar-item .bar .percentage {
      height: 5px;
      border-radius: 5px;
      background-color: #754ef9; }
