.timeline {
  position: relative;
  display: flex;
  flex-flow: column wrap;
  justify-content: center; }
  .timeline .icon {
    display: flex;
    flex-flow: row wrap; }
    .timeline .icon .contour {
      display: flex;
      width: 50px;
      height: 50px;
      border: 4px solid #d9d9d9;
      border-radius: 100%;
      align-items: center;
      flex-flow: row wrap;
      justify-content: center; }
      .timeline .icon .contour i {
        color: #754ef9; }
  .timeline::before {
    position: absolute;
    bottom: 0;
    left: 27px;
    width: 4px;
    height: calc(100% - 58px);
    content: '';
    background: #d9d9d9 linear-gradient(to bottom, #d9d9d9 1%, #d9d9d9 75%, #fbfbfb 100%); }
  .timeline .events {
    display: flex;
    padding-left: 58px;
    flex-flow: column wrap; }
