.btn {
  width: fit-content;
  padding: 0 40px;
  cursor: pointer;
  user-select: none;
  transition: all 0.3s ease-out 0s;
  border: 1px solid #754ef9;
  border-radius: 50px;
  background-color: #754ef9;
  box-shadow: 0 5px 8px 0 #754ef9;
  line-height: 48px;
  text-transform: uppercase;
  color: #ffffff;
  font-family: inherit;
  font-size: 15px;
  font-weight: 500; }
  .btn:hover {
    color: #754ef9;
    background-color: #ffffff; }
