.header {
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: center;
  flex-flow: column wrap; }
  .header .dynamic-background {
    position: absolute;
    top: 0;
    left: -100px;
    width: calc(100% + 200px);
    height: 100%;
    transition: all 0.25s ease-out 0s;
    background: url("../../../assets/images/techology-network.png") no-repeat center;
    background-size: cover; }
  .header .content {
    z-index: 1;
    display: flex;
    align-items: center;
    flex-flow: row wrap; }
    .header .content .introduction {
      flex: 1; }
      @media (min-width: 1000px) {
        .header .content .introduction .name {
          text-transform: uppercase;
          color: #32333c;
          font-family: inherit;
          font-size: 60px;
          font-weight: 600; } }
      @media (max-width: 1000px) {
        .header .content .introduction .name {
          text-transform: uppercase;
          color: #32333c;
          font-family: inherit;
          font-size: 38px;
          font-weight: 600; } }
      @media (min-width: 1000px) {
        .header .content .introduction .greeting {
          text-transform: uppercase;
          color: #754ef9;
          font-family: inherit;
          font-size: 30px;
          font-weight: 600; } }
      @media (max-width: 1000px) {
        .header .content .introduction .greeting {
          text-transform: uppercase;
          color: #754ef9;
          font-family: inherit;
          font-size: 24px;
          font-weight: 600; } }
      .header .content .introduction .description {
        padding-bottom: 30px; }
    @media (min-width: 1000px) {
      .header .content {
        width: 1000px;
        padding-top: 150px; }
        .header .content .avatar {
          position: relative;
          display: flex;
          flex: 1;
          flex-flow: row wrap;
          justify-content: center; }
          .header .content .avatar img {
            position: relative;
            width: 300px;
            height: 450px; }
          .header .content .avatar::before {
            position: absolute;
            top: 60px;
            width: 265px;
            height: 350px;
            content: '';
            border: 20px solid #754ef9; }
          .header .content .avatar::after {
            position: absolute;
            bottom: 0;
            width: 305px;
            height: 20px;
            content: '';
            background-color: #754ef9; } }
    @media (max-width: 1000px) {
      .header .content {
        width: auto;
        padding-top: 200px; }
        .header .content .avatar {
          display: none; } }
  .header .social-networks {
    position: relative;
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start; }
    .header .social-networks::before {
      position: absolute;
      top: 50%;
      left: -100%;
      width: 100%;
      height: 2px;
      content: '';
      transform: translateY(-50%);
      background-color: #754ef9; }
    @media (min-width: 1000px) {
      .header .social-networks {
        width: 1000px;
        margin-top: 0;
        padding: 0; } }
    @media (max-width: 1000px) {
      .header .social-networks {
        width: calc(100% - 30px);
        margin-top: 50px; } }
