@media (min-width: 1000px) {
  .nav-menu .content {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-end; } }

@media (max-width: 1000px) {
  .nav-menu {
    position: absolute;
    top: 95px;
    left: 0;
    width: 100%;
    margin: 0;
    padding: 0; }
    .nav-menu .content {
      display: flex;
      overflow: hidden;
      flex-direction: column;
      margin: 0 15px;
      transition: max-height 0.3s ease-out 0s;
      background-color: #ffffff;
      box-shadow: 0 10px 15px 0 #868686; }
      .nav-menu .content.is-visible {
        max-height: 200px;
        padding: 3px 12px; }
      .nav-menu .content.is-invisible {
        max-height: 0;
        padding: 0 12px; } }
