.event {
  padding: 20px 0; }
  .event .organization {
    text-transform: inherit;
    color: #32333c;
    font-family: inherit;
    font-size: 18px;
    font-weight: 500;
    position: relative; }
    .event .organization::before {
      position: absolute;
      left: -40px;
      width: 14px;
      height: 14px;
      content: '';
      border: 4px solid #fbfbfb;
      border-radius: 100%;
      background-color: #754ef9; }
  .event .role {
    text-transform: inherit;
    color: #32333c;
    font-family: "Poppins", sans-serif;
    font-size: 16px;
    font-weight: 400;
    padding-bottom: 15px; }
  .event .duration {
    text-transform: inherit;
    color: inherit;
    font-family: "Poppins", sans-serif;
    font-size: 14px;
    font-weight: 100;
    padding-bottom: 10px; }
